/* =============================================================================
                            .home-page (Home Component)
============================================================================= */


.home-page {
	flex: 1 1;
	padding-top: 50px;
	padding-bottom: 50px;
	color: white;
	background: #D3CCE3;                                               /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, left bottom, from(#E9E4F0), to(#D3CCE3));
  background: linear-gradient(to bottom, #E9E4F0, #D3CCE3);          /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.avatar-figure {
	display: block;
	position: relative;
	top: 0;
	left: 0;
	margin: 0 auto 25px auto;
	padding: 0;
	height: 300px;
	width: 300px;
	background-color: rgba(255,255,255,1);
	background-color: rgba(125,125,100,0.5);
	background-image: url(/react-mock-portfolio-site/static/media/halftone.e102fba9.png);
	background-size: contain;
	background-blend-mode: overlay;
	border: 1px solid black;
	border-radius: 300px;
	overflow: hidden;
	box-shadow: -5px 5px 5px rgba(0,0,0,0.15);
}

.avatar-img {
	display: block;
	position: absolute;
	bottom: 0px;
	left: 0;
	width: 100%;
}

.banner-text {
	width: 75%;
	margin: 0 auto;
	padding: 25px;
	background-color: #27221F;
	border: 2px solid black;
	border-radius: 10px;
}

.banner-text h2 {
	margin-bottom: 15px;
	text-align: center;
	font-size: 40px;
}

.banner-text .fancy-hr {
	border-color: violet;
}

.banner-text p {
}

.social-links {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}

.social-links > a {
	display: inline-block;
	margin: 0;
	padding: 0 10px;
	text-decoration: none;
	font-size: 40px;
	color: white;
	background-color: transparent;
}

.social-links > a:hover i {
	color:  #61DAFB;
}


