/* =============================================================================
                               .tab-container
============================================================================= */


.tab-container{}

.tab-container .tab-buttons-container {
	display: flex;
	justify-content: center;
	margin-bottom: 25px;
}

.tab-container .tab-buttons-container .btn:active{
	box-shadow: none;
	outline: none;
}

.tab-container .btn.btn-primary {
	color: white;
	background-color: #61DAFB;
	border: 1px solid rgba(0,0,0,0.15);
	box-shadow: -2px 2px 4px rgba(0,0,0,0.15);
}

.tab-container .btn.btn-primary:active {
	background-color: violet !important;
}

/* .tab-content is a Bootstrap class, otherwise I would use .tab-content-container. */
.tab-container .tab-content {
	min-height: 300px;
}

.tab-container .tab-content .tab-pane {
  color: black;
}

.tab-container .tab-content .tab-pane .fancy-h1 {

}

.tab-container .tab-content .tab-pane .card-container {
	display: flex;
	justify-content: center;
	flex-wrap:wrap;
}

.tab-container .tab-content .tab-pane .card-container .card {
	flex: 0 1 26%; /* 26% ensures that there will never be more thatn 3 items per 'line' */
  min-width: 300px;
	margin: 0 12.5px 25px 12.5px;
	margin: 0 20px 40px 20px;
	padding: 0;
	border: 1px solid violet;
	border-radius: 10px;
	box-shadow: none;
	overflow: hidden;
	-webkit-transition: box-shadow 0.15s linear, -webkit-transform 0.15s linear;
	transition: box-shadow 0.15s linear, -webkit-transform 0.15s linear;
	transition: box-shadow 0.15s linear, transform 0.15s linear;
	transition: box-shadow 0.15s linear, transform 0.15s linear, -webkit-transform 0.15s linear;
}

.tab-container .tab-content .tab-pane .card-container .card:hover{
	box-shadow: -5px 5px 5px rgba(0,0,0,0.15);
	-webkit-transform: scale(1.025);
	        transform: scale(1.025);
}


