/* =============================================================================
                              .progress-container
============================================================================= */


.progress-container {
	display: flex;
	align-items: center;
	margin-bottom: 25px;
}

.progress-title-container {
	flex: 1 1 30%;
	min-width: 150px;
}

.progress-bar-container {
	flex: 1 1 70%;
	position: relative;
	padding: 3px;

	border-radius: 15px;
	overflow: hidden;

	background-color: white;
	border: 1px solid black;
	box-shadow: inset  1px 1px 1px rgba(0,0,0,0.75),
	            inset -1px -1px 1px rgba(0,0,0,0.75);
}

.progress-value-div {
	position: absolute;
	bottom: 0;
	left: 0;
	height: 100%;
	width: 100%;
	color: black;
	font-size: 12px;
	font-weight: bold;
	line-height: 26px; /* matches height+padding of progress to center text. */
}

progress {
	display: block; /* Removes bottom spacing. */
  height: 20px;
  width: 100%;
	-webkit-appearance: none;
	   -moz-appearance: none;
	        appearance: none;

	border-radius: 15px;
	overflow: hidden;
}

progress::-webkit-progress-bar {
  background-color: white;
}

progress::-webkit-progress-value {
  background: linear-gradient(
    45deg, transparent 33%, rgba(0, 0, 0, .1) 33%, rgba(0, 0, 0, .1) 66%, transparent 66%
  );
  background-color: gray; /* Default */
  background-size: 40px 20px;
}

progress.html::-webkit-progress-value       { background-color: #FF355E; }
progress.css::-webkit-progress-value        { background-color: #FD5B78; }
progress.sass::-webkit-progress-value       { background-color: #FF6037; }
progress.bootstrap::-webkit-progress-value  { background-color: #FF9966; }
progress.javascript::-webkit-progress-value { background-color: #FF9933; }
progress.jquery::-webkit-progress-value     { background-color: #FFCC33; }
progress.react::-webkit-progress-value      { background-color: #FFFF66; }
progress.php::-webkit-progress-value        { background-color: #CCFF00; }
progress.node::-webkit-progress-value       { background-color: #66FF66; }
progress.sql::-webkit-progress-value        { background-color: #AAF0D1; }
progress.mongo::-webkit-progress-value      { background-color: #50BFE6; }
progress.firebase::-webkit-progress-value   { background-color: #FF6EFF; }
progress.github::-webkit-progress-value     { background-color: #FF00CC; }


